import {html, render} from 'uhtml'

export class InputNumber extends HTMLElement {
    press_repeat_threshold: number = null;
    _press_repeat_threshold = null;
    press_repeat_interval: number = null;
    _press_repeat_interval = null;
    change_event_delay: number = null;
    _change_event_delay = null;
    old_value: string = null;
    el = {
        input: null,
    };

    constructor() {
        super();
        this.el['input'] = this.querySelector('input') ||
            html`<input type="number" value="0">`;
        this.press_repeat_threshold = parseInt(
            this.getAttribute('press_repeat_threshold') || "700"
        );
        this.press_repeat_interval = parseInt(
            this.getAttribute('press_repeat_interval') || "100"
        );
        this.change_event_delay = parseInt(
            this.getAttribute('change_event_delay') || "1000"
        );
    }

    render() {
        render(this, html`
            ${this.el['input']}
            <pkt-button-square el="down"
                @mousedown=${(e) => this.fast_down_start()}
                @mouseup=${(e) => { this.press_stop() }}
                @mouseout=${(e) => { this.press_stop() }}
            >-</pkt-button-square>
            <pkt-button-square el="up"
                @mousedown=${(e) => this.fast_up_start()}
                @mouseup=${(e) => {this.press_stop()}}
                @mouseout=${(e) => {this.press_stop()}}
            >
            +</pkt-button-square>
        `);
        this.el['input'] = this.querySelector('input');
    }

    connectedCallback() {
        this.render();
        if (!this.el.input.value) {
            this.el.input.value = this.el.input.getAttribute("min") || "0";
        }
        this.el['input'].addEventListener('change', () => {
            this.emit_change_event();
        })
    }

    get value() {
        return this.querySelector('input').value;
    }

    set value(value) {
        this.querySelector('input').value = value;
    }

    emit_change_event() {
        clearTimeout(this._change_event_delay);
        if (this.old_value == this.value) {
            return;
        }
        this._change_event_delay = setTimeout(() => {
            this.dispatchEvent(new Event('change'));
            this.old_value = this.value;
        }, this.change_event_delay);
    }

    fast_up_start() {
        this.el.input.stepUp();
        this._press_repeat_threshold = setTimeout(() => {
            this._press_repeat_interval = setInterval(() => {
                this.el['input'].stepUp()
            }, this.press_repeat_interval)
        }, this.press_repeat_threshold)
    }

    fast_down_start() {
        this.el.input.stepDown();
        this._press_repeat_threshold = setTimeout(() => {
            this._press_repeat_interval = setInterval(() => {
                this.el['input'].stepDown()
            }, this.press_repeat_interval)
        }, this.press_repeat_threshold)
    }

    press_stop() {
        if (this._press_repeat_threshold) {
            clearTimeout(this._press_repeat_threshold);
            clearInterval(this._press_repeat_interval);
            this._press_repeat_threshold = null;
            this._press_repeat_interval = null;
            this.emit_change_event();
        }
    }
}
