import { html, svg, render } from 'uhtml';
import {ProgressBar} from '../progress/progress-bar';


export class FileManager extends HTMLElement {
    el: object = {};
    upload_url: string = './';

    constructor() {
        super();
        let tag_prefix = this.tagName.toLowerCase();
        customElements.define(`${tag_prefix}-file-upload`, FileUpload);
    }

    _add_file(file: File) {
        let el_file_upload = new FileUpload();
        el_file_upload.upload_url = this.upload_url;
        this.el['dashboard'].appendChild(el_file_upload);
        el_file_upload.file = file;
    }

    render() {
        render(this, html`
        <input el="input" type="file" accept="image/*" multiple style="display: none">
        <div el="toolbar">
            <div class="left">
                <button class="add" @click=${() => {this.open_file_selector()}}>Add</button>
                <button class="upload" @click=${() => {this.send()}}>Upload All</button>
            </div>
            <div class="right">
                <button class="bg-red" @click=${() => {this.delete()}}>Delete All</button>
            </div>
        </div>
        <div el="dashboard"></div>
        `);

        this.el['input'] = this.querySelector('[el="input"]');
        this.el['toolbar'] = this.querySelector('[el="toolbar"]');
        this.el['dashboard'] = this.querySelector('[el="dashboard"]');
    }

    connectedCallback() {
        this.render();
        this.upload_url = this.getAttribute('upload_url') || this.upload_url;
        let input = this.querySelector('input[type="file"');
        input.addEventListener('change', (event) => {
            this.on_file_selected(input);
        });
    }

    on_file_selected(input) {
        for (let file of input.files) {
            this._add_file(file);
        }
        this.el['input'].value = null;
    }

    open_file_selector() {
        this.el['input'].click();
    }

    add_file_from_url(url) {
        let el_file_upload = new FileUpload();
        this.el['dashboard'].appendChild(el_file_upload);
        el_file_upload.url = url
    }


    async send(method="POST") {
        let tag = this.tagName.toLowerCase();
        let el_file_upload_list: NodeListOf<FileUpload> =
            this.querySelectorAll(`${tag}-file-upload`);
        for (let el_file_upload of el_file_upload_list) {
            await el_file_upload.send(method);
        }
    }

    async delete() {
        let tag = this.tagName.toLowerCase();
        let el_file_upload_list: NodeListOf<FileUpload> =
            this.querySelectorAll(`${tag}-file-upload`);
        for (let el_file_upload of el_file_upload_list) {
            await el_file_upload.delete();
        }
    }
}

export class FileUpload extends HTMLElement {
    upload_url: string = './';
    _file: File;
    _url: string;
    xhr: XMLHttpRequest = new XMLHttpRequest();
    el: object = {
        "file": undefined,
        "image": undefined,
        "upload": undefined,
        "delete": undefined,
        "progress_bar": undefined
    };
    icon: object = {
        "delete": svg`
            <svg
                    viewBox="0 0 16 16"
                    style="width: 1em; height: 1em;"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlns:svg="http://www.w3.org/2000/svg">
                <path
                    d="M4 4h1.031c.255.011.51.129.688.313L8 6.592l2.312-2.28c.266-.231.447-.306.688-.313h1v1c0 .286-.034.55-.25.75L9.469 8.031l2.25 2.25c.188.188.281.454.281.719v1h-1c-.265 0-.53-.093-.719-.281L8 9.438l-2.281 2.28A1.015 1.015 0 015 12H4v-1c0-.265.093-.53.281-.719l2.281-2.25-2.28-2.281A.909.909 0 014 5z"
                    style="line-height:normal;-inkscape-font-specification:'Andale Mono';text-indent:0;text-align:start;text-decoration-line:none;text-transform:none;marker:none;stroke:#ffffff;stroke-opacity:1;stroke-width:0.5;stroke-miterlimit:4;stroke-dasharray:none"
                    color="#bebebe"
                    font-weight="400"
                    font-family="Andale Mono"
                    overflow="visible"
                    fill="#2e3436"
                    id="path2" />
            </svg>
        `,
        "upload": svg`
            <svg
                viewBox="0 0 16 16"
                style="width: 1em; height: 1em;"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                xmlns:svg="http://www.w3.org/2000/svg">
                <g color="#000" fill="#2e3436">
                    <path
                        id="path2"
                        style="marker:none;stroke:#ffffff;stroke-opacity:1;stroke-width:0.5;stroke-miterlimit:4;stroke-dasharray:none"
                        d="M 3 1 A 1 1 0 0 0 2 2 L 2 15 A 1 1 0 0 0 3 16 L 6 16 L 6 14 L 4 14 L 4 3 L 9.5859375 3 L 12 5.4140625 L 12 14 L 10 14 L 10 16 L 13 16 A 1 1 0 0 0 14 15 L 14 5 A 1 1 0 0 0 13.707031 4.2929688 L 10.707031 1.2929688 A 1 1 0 0 0 10 1 L 3 1 z M 8 6.5859375 L 5.2929688 9.2929688 C 5.1122188 9.4737187 5 9.723 5 10 L 5 11 L 6 11 C 6.277 11 6.5262813 10.887781 6.7070312 10.707031 L 7 10.414062 L 7 16 L 9 16 L 9 10.414062 L 9.2929688 10.707031 C 9.4737187 10.887781 9.723 11 10 11 L 11 11 L 11 10 C 11 9.723 10.887781 9.4737187 10.707031 9.2929688 L 8 6.5859375 z " />
                </g>
            </svg>
        `
    };

    constructor() {
        super();
        let tag_prefix = this.tagName.toLowerCase();
        try {
            customElements.define(`${tag_prefix}-progress-bar`, ProgressBar);
        } catch (error) {};
        let progress_bar = new ProgressBar();
        progress_bar.value = "0%";
        this.el["progress_bar"] = progress_bar;
    }

    get file() {
        return this._file;
    }

    set file(file: File) {
        this._file = file;
        if (!file) {return};
        let objectURL = window.URL.createObjectURL(file);
        this.url = objectURL;
        let image: HTMLImageElement = this.querySelector('[el="image"]');
        image.addEventListener('load', () => {
            URL.revokeObjectURL(image.src);
        });
    }

    get is_uploaded(): boolean {
        if (!this.file && this.url) {
            return true;
        } else {
            return false;
        }
    }

    get url() {
        return this._url;
    }

    set url(url) {
        this._url = url;
        this.render();
    }

    render() {
        render(this, html`
        <div el="grid" style="--aspect-ratio: 1;">
            <div>
                <div el="file"><img el="image" src="${this.url}"></div>
                <div class="button bg-red" el="delete" @click=${() => {this.delete()}}>
                    ${this.icon['delete']}
                </div>
                <div class="button" el="upload" @click=${() => {this.send()}}>
                    ${this.icon['upload']}
                </div>
                ${this.el["progress_bar"]}
            </div>
        </div>
        `);
        this.el['file'] = this.querySelector('[el="file"]');
        this.el['image'] = this.querySelector('[el="image"');
        this.el['delete'] = this.querySelector('[el="delete"]');
        this.el['upload'] = this.querySelector('[el="upload"]');

        if (this.is_uploaded == true) {
            this.el['upload'].style.display = 'none';
            this.el['progress_bar'].style.display = 'none';
        }
    }

    connectedCallback() {
        this.render();
    }

    async send(method="POST") {
        this.xhr.upload.addEventListener(
            "progress",
            (event) => {this.on_upload_progress(event)}
        );
        this.xhr.onreadystatechange = () => {
            if (this.xhr.readyState == 4 && this.xhr.status == 200) {
                this.on_upload_completed();
            }
        };

        let formData = new FormData();
        formData.append('file', this.file);
        this.xhr.open(method, this.upload_url);
        this.xhr.send(formData);
    }

    async delete() {
        if (!this.file) {
            try {
                let response = await fetch(this.url, {
                    method: 'DELETE',
                });
                response = await response.json();
            }
            catch (err) {
                console.log(err);
            };
        };
        if (this.xhr.readyState >= 1 && this.xhr.readyState <= 3) {
            this.xhr.abort();
        };
        this.remove();
        // let event = new CustomEvent('delete');
        this.dispatchEvent(new CustomEvent('delete'));
    }

    on_upload_progress(progress_event: ProgressEvent) {
        if (progress_event.lengthComputable) {
            let percent = Math.round(
                (progress_event.loaded * 100) / progress_event.total
            );
            this.el['progress_bar'].value = `${percent}%`;
            let event = new CustomEvent('progress', {
                detail: {"progress_event": progress_event}
            });
            this.dispatchEvent(event);
        }
    }

    async on_upload_completed() {
        this.url = this.xhr.response;
        this.file = null;
        await new Promise(r => setTimeout(r, 1000));
        this.render();
        this.dispatchEvent(new Event('upload_completed'));
    }
}