import {html, svg, render} from 'uhtml';

export class InputDate extends HTMLElement {
    icon: any;

    constructor() {
        super();
        this.icon = svg`
        <svg xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 16 16" style="width: 1em; height: 1em;">
            <g fill="currentColor">
                <path d="M2.969 1.01c-1.053 0-2 .846-2 1.938v10.115c0 1.092.947 1.937 2 1.937H13c1.052 0 2-.845 2-1.937V2.948c0-1.092-.948-1.937-2-1.937zm0 3.99H5v2H2.969zM6 5h3v2H6zm4 0h3v2h-3zM2.969 8H5v2H2.969zM6 8h3v2H6zm4 0h3v2h-3zm-7.031 3H5v2H2.969zM6 11h3v2H6zm4 0h3v2h-3z" overflow="visible" />
                <path d="M10 8h3v2h-3z" overflow="visible" opacity=".35" />
            </g>
        </svg>
        `
    }

    set value(value) {
        this.querySelector('input').value = value;
    }

    get value() {
        return this.querySelector('input').value;
    }

    get valueAsDate() {
        return this.querySelector('input').valueAsDate;
    }

    get valueAsNumber() {
        return this.querySelector('input').valueAsNumber;
    }

    connectedCallback() {
        this.render();
    }

    on_change(event) {
        this.querySelector('[el="display"]').innerText = event.target.value;
    }

    render() {
        render(this, html`
            <div el="icon">${this.icon}</div>
            <input name=${this.getAttribute('name')} type="date" @change=${(event) => { this.on_change(event) }}>
            <span el="display"></span>
        `);
    }
}