import {html, render} from 'uhtml'

export class Switch extends HTMLElement {
    constructor() {
        super();
    }

    render() {
        let checkbox = this.querySelector('input[type="checkbox"]') ||
            html`<input type="checkbox">`
        let label = this.querySelector('div[el="label"]') ||
            html`
                <div el="label">
                    <span>ON</span>
                    <span>OFF</span>
                </div>
            `;
        render(this, html`
            ${checkbox}
            ${label}
            <div el="paddle"></div>
        `)
    }

    connectedCallback() {
        this.render();
    }
}