import {html, render} from 'uhtml';

export class ProgressBar extends HTMLElement {
    _value: string = undefined;

    constructor() {
        super();
        this.value = this.getAttribute("value") || undefined;
    }

    static get observedAttributes() {
        return ['value'];
    }

    get value() {
        return this._value;
    }

    set value(value) {
        this.setAttribute("value", value);
        this._value = value;
        this.render_value();
    }

    render_loop() {
        render(this, html`<div class="loop"></div>`);
    }

    render_value() {
        render(this, html`
            <div class="value" style=${`width: ${this.value};`}"></div>
        `);
    }

    connectedCallback() {
        if (this.value) {
            this.render_value();
        } else {
            this.render_loop();
        };
    }

    // attributeChangedCallback(name, old_value, new_value) {
    //     console.log('attribute change');
    //     if (name === "value") {
    //         this.value = new_value;
    //         this.render_value();
    //     }
    // }
}