import {svg, render} from 'uhtml';

export class Spinner extends HTMLElement {
    constructor() {
        super();
    }

    render() {
        render(this, svg`
            <svg viewBox="25 25 50 50">
                <circle cx="50" cy="50" r="20" stroke-miterlimit="10"/>
            </svg>
        `);
    }

    connectedCallback() {
        this.render();
    }
}