import {html, render} from 'uhtml';

export class Checkbox extends HTMLElement {
    constructor() {
        super();
        this.el = {};
        this.el['checkbox'] = this.querySelector('input[type="checkbox"]') ||
            html`<input type="checkbox">`;
    }

    render() {
        render(this, html`
        ${this.el['checkbox']}
        <span></span>
        `);
    }

    connectedCallback() {
        this.render();
    }
}
