import {TagX} from '../tag/tag-x.js';

export class InputTag extends HTMLElement {
    constructor() {
        super();
        this.el = {};
        this.el['input'] = document.createElement('input');
        this.appendChild(this.el['input']);
        this.addEventListener('click', () => {
            this.el['input'].focus();
        })
        this.el['input'].addEventListener('keydown', (event) => {
            this.keydown(event);
        })
        this.el['input'].addEventListener('blur', (event) => {
            this.blur();
        })

        let tag = this.tagName.toLocaleLowerCase();
        customElements.define(`${tag}-tag-x`, TagX);
    }

    keydown(event) {
        if (event.key === ("Enter")) {
            this.add_tag(this.el['input'].value.trim());
            this.el['input'].value = '';
        } else if (event.key === "Backspace") {
            if (this.el['input'].value.trim() === '') {
                this.el['input'].previousSibling.remove();
            }
        }
    }

    blur() {
        this.add_tag(this.el['input'].value.trim());
        this.el['input'].value = '';
    }

    add_tag(value) {
        if (value == '') {return};
        let tag_x_name = `${this.tagName.toLocaleLowerCase()}-tag-x`;
        for (let el of this.querySelectorAll(tag_x_name)) {
            if (el.innerText.trim() === value) { return }
        }
        let tag_x = new TagX;
        this.insertBefore(tag_x, this.el['input']);
        tag_x.querySelector('span').innerText = value;
        tag_x.addEventListener('remove', (event) => {
            this.dispatchEvent(new CustomEvent('remove', {
                detail: {tag_x: event.target}
            }))
        })
    }

    get tags() {
        let tag_x_name = `${this.tagName.toLocaleLowerCase()}-tag-x`;
        let tag_x_nodes = this.querySelectorAll(tag_x_name);
        let tags = [];
        for (let tag of tag_x_nodes) {
            tags.push(tag.innerText.trim());
        }
        return tags;
    }
}
