import {html, render} from 'uhtml';

export class DotPulse extends HTMLElement {
    constructor() {
        super();
    }

    render() {
        render(this, html`
            <div data-id="p1"></div>
            <div data-id="p2"></div>
            <div data-id="p3"></div>
        `);
    }

    connectedCallback() {
        this.render();
    }
}