import { html, render } from 'uhtml';
import { Editor } from '@tiptap/core';
import StarterKit from '@tiptap/starter-kit';
import FloatingMenu from '@tiptap/extension-floating-menu';


class Heading extends HTMLElement {
    editor = Editor;
    _levels: number[];

    constructor() {
        super();
        this.addEventListener('click', (event) => {
            this.on_click(event);
        });
    }

    init({levels=[1,2,3], editor=null} = {}) {
        this.levels = levels;
        this.editor = editor;
        this.editor.on('selectionUpdate', () => {
            this.updateActiveStatus();
        })
    }

    set levels(values: number[]) {
        this._levels = values;
        this.render();
    }

    get levels() {
        return this._levels;
    }

    render() {
        render(this, html`
            ${this.levels.map((level) => html`
                <pkt-input-radio-btn>
                    <input type="radio" name="heading" value="${level}">
                    <button>H${level}</button>
                </pkt-input-radio-btn>
            `)}
        `);
    }

    on_click(event) {
        let el_input: HTMLInputElement = event.target;
        let level = parseInt(el_input.value);
        this.editor.chain().focus()
            .toggleHeading({level: level})
            .run();
        if (!this.editor.isActive('heading', {level: level})) {
            el_input.checked = false;
        }
    }

    updateActiveStatus() {
        for (let el_input of this.querySelectorAll('input')) {
            let level = parseInt(el_input.value);
            if (this.editor.isActive('heading', { level: level })) {
                el_input.checked = true;
            } else {
                el_input.checked = false;
            }
        }
    }

    connectedCallback() {
        this.updateActiveStatus();
    }
}

export class HTMLEditor extends HTMLElement {
    editor: Editor;
    template: any;

    constructor() {
        super();
        let tag = this.tagName.toLowerCase();
    };

    connectedCallback() {
        let el_editor = document.createElement('div');
        el_editor.setAttribute('el', 'editor');

        let el_float_menu = document.createElement('div');
        el_float_menu.setAttribute('el', 'float-menu');

        this.template = html`
        ${el_float_menu}
        ${el_editor}
        `;

        this.render();

        this.editor = new Editor({
            element: el_editor,
            extensions: [
                StarterKit,
                FloatingMenu.configure({
                    element: el_float_menu,
                }),
            ],
        });

        let tag = this.tagName.toLowerCase();
        try {
            customElements.define(`${tag}-heading`, Heading);
        } catch (err) {
            console.log(err);
        };
        let el_heading = new Heading();
        el_heading.setAttribute('el', 'heading');
        el_heading.init({levels: [1,2,3], editor: this.editor});
        el_heading.render();
        el_float_menu.append(el_heading);
    }

    render() {
        render(this, this.template);
    }
};